import {Component, OnInit} from '@angular/core';
import {ApiService} from "../api.service";

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  info: object = null;
  info_keys: Array<string> = [];

  constructor(private apiService: ApiService) {
  }

  ngOnInit() {
    this.getMe()
  }

  public getMe(): void {
    this.apiService.getMe().subscribe((response: object) => {
      this.info = response;
      this.info_keys = Object.keys(this.info);
      console.log(response);
      console.log(Array.isArray(response['car_set']));
    });
  }

  public edit(data): void{
     this.apiService.editMe(data).subscribe((response: object) => {
      this.info = response;
      this.info_keys = Object.keys(this.info);
      console.log(response);
    });
  }
}




