import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';

@Injectable()
export class ApiService {
  api_url: string = 'http://127.0.0.1:8000/api/v1/';

  constructor(private  httpClient: HttpClient) {

  }

  getList(list_name) {
    return this.httpClient.get(`${this.api_url}${list_name}/`);
  }

  getItem(list_name, item_id) {
    return this.httpClient.get(`${this.api_url}${list_name}/${item_id}/`);
  }

  getMe() {
    return this.httpClient.get(`${this.api_url}me/`);
  }

  editMe(data) {
    return this.httpClient.put(`${this.api_url}me/`, data, {headers: new HttpHeaders({'X-CSRFTOKEN': this.getCsrfToken()})});
  }

  getCsrfToken() {
    const cookies = document.cookie.split(';');
    for (let cookie of cookies) {
      cookie = cookie.trim();
      const re = new RegExp('^csrftoken=');
      if (re.test(cookie)) {
        return cookie.substr(10, cookie.length);
      }
    }
  }

  createItem(list_name, data) {
    return this.httpClient.post(`${this.api_url}${list_name}/`, data, {headers: new HttpHeaders({'X-CSRFTOKEN': this.getCsrfToken()})});
  }

  register(data) {
    return this.httpClient.post(`${this.api_url}register/`, data, {headers: new HttpHeaders({'X-CSRFTOKEN': this.getCsrfToken()})});
  }

  login(data) {
    return this.httpClient.post(`${this.api_url}login/`, data, {headers: new HttpHeaders({'X-CSRFTOKEN': this.getCsrfToken()})});
  }

  logout() {
    return this.httpClient.post(`${this.api_url}logout/`, null, {headers: new HttpHeaders({'X-CSRFTOKEN': this.getCsrfToken()})});
  }
}



